import request from '../utils/request.js'
export function getShopCarList(data){
    return request({
        url:'/api/shoplist',
        method: 'get',
        params: {
            ...data
        }
    })
}
export function reduceShopCar(data){
    return request({
        url: '/api/remove',
        method: 'get',
        params: {
            ...data
        }
    })
}

export function deleteShopCar(data){
    return request({
        url: '/api/del',
        method: 'get',
        params: {
            ...data
        }
    })
}