import request from '../utils/request.js'

export function getDetail(goodId){
    return request({
        url: '/api/detail',
        method:'get',
        params: {
            goodId
        }
    })
}

export function getShopList(type_one){
    return request({
        url: '/api/goodList',
        method: 'get',
        params: {
            type_one
        }
    })
}

export function addShopCar(data){
    // goodid username token
    return request({
        url:"/api/add",
        method:"get",
        params:{
            ...data,
        }
    })
}