<template>
  <div class="shopCar">
    <div class="shop">
      <div class="carHeader">
        <input type="checkbox" v-model="isAll" @change="changeAll" />
        <span class="all">全选</span>
        <span class="name">商品详情</span>
        <span class="pri">单价</span>
        <span class="number">数量</span>
        <span class="price">总格</span>
        <span class="handle">操作</span>
      </div>
      <div class="carMain" v-for="(item, index) in carList" :key="index">
        <div class="mainItem">
          <input
            class="done"
            :value="item"
            v-model="checkeds"
            type="checkbox"
          />
          <img :src="item.img_list_url" alt="" />
          <span class="name">{{ item.title }}</span>
          <span class="pri">￥{{ item.price }}</span>
          <div class="num">
            <button @click="reduce(item)">-</button>
            <span type="text">{{ item.count }}</span>
            <button @click="add(item.Id)">+</button>
          </div>
          <span class="price">{{ item.price * item.count }}￥</span>
          <el-button
            type="info"
            round
            @click="deleteShopCar(item.Id)"
            class="dele"
            >删除</el-button
          >
        </div>
      </div>
      <div class="carFooter">
        <input type="checkbox" v-model="isAll" @change="changeAll" />
        <span class="all">全选</span>
        <span class="delect">全部删除</span>
        <span class="add">总价：￥{{ totalPrice }}</span>
        <el-button type="danger" round class="dele">结算</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getShopCarList,
  reduceShopCar,
  deleteShopCar,
} from "../../api/shopCar.js";
import { addShopCar } from "../../api/detail.js";
import { _debounce } from "../../components/double.js";
import { set, get } from "../../utils/localStrage.js";
export default {
  name: "ShopCar",
  data() {
    return {
      carList: [], // 购物车列表数据
      flag: true, // 防止多次点击加加
      checkeds: [], // 存放多选列表
      isAll: false, // 是否全选
      totalPrice: 0,
    };
  },
  methods: {
    reduce(item) {
      if (item.count > 1) {
        let { username, token } = this.$store.state.user;
        let goodId = item.Id;
        reduceShopCar({ token, username, goodId }).then((data) => {
          this.getShopCarList();
        });
      }else{
        this.$message({
          type: 'info',
          message: '商品已减到最少!!!'
        })
      }
    },
    deleteShopCar(goodId) {
      this.$confirm("确定删除该商品???", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          let { username, token } = this.$store.state.user;
          deleteShopCar({ token, username, goodId })
            .then(() => {
              this.getShopCarList();
              this.$message({
                type: "success",
                message: "商品已删除",
              });
            })
            .catch(() => {
              this.$message({
                type: "danger",
                message: "商品删除失败，请刷新后重试",
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    add(goodId) {
      if (this.flag) {
        this.falg = false;
        let { token, username } = this.$store.state.user;
        addShopCar({ token, username, goodId }).then((data) => {
          this.falg = true;
          this.getShopCarList();
        });
      }
    },
    getShopCarList() {
      let { token, username } = this.$store.state.user;
      getShopCarList({ token, username }).then((data) => {
        console.log("获取成功", data);
        this.carList = data;
      });
    },
    changeAll() {
      if (this.isAll) {
        this.checkeds = this.carList;
      } else {
        this.checkeds = [];
      }
    },
  },
  /**
   * computed 计算属性 值是对象
   * key 值都是getter函数 getter 函数特点: 每个函数都有return
   * 注意 key不是函数 key作用是板寸getter函数的返回值
   * getter 函数执行条件
   * 1: 当使用key多次使用key getter函数执行一次
   * 2: getter 函数中依赖的数据发生变化 重新执行
   */
  // computed: {
  //   totalPrice() {
  //     let res = 0;
  //     this.checkeds.forEach(item => {
  //       res += parseInt(item.count)*parseFloat(item.price);

  //     });
  //     return res;
  //   },
  // },
  created() {
    this.getShopCarList();
    // this.checkeds = get("checkeds");
  },
  mounted() {},
  beforeUpdate() {
    // this.checkeds = get("checkeds");
  },
  updated() {},
  /**
   *
   * watch 监听属性 监听所有属性的响应式数据 当响应式数据变化handler函数执行
   * 赋值: 对象
   * key 被监听的响应式数据
   * value: objct | function
   *        function 就是handler函数
   *        object 有两个属性
   *            deep: 是否深度监听  true 深度监听 false 不深度监听
   *            handler 赋值是函数
   */
  watch: {
    checkeds: {
      deep: true, // 对象数组深度监听
      handler(val) {
        let res = 0;
        val.forEach((item) => {
          res += parseInt(item.count) * parseFloat(item.price);
        });
        this.totalPrice = res;
        if (val.length === this.carList.length) {
          this.isAll = true;
        } else {
          this.isAll = false;
        }
      },
    },
  },
};
</script>

<style lang="less" scoped>
.shopCar {
  width: 100vw;
  height: 100vh;
  background-color: #e8e4e4;
  padding-top: 50px;
}
.shop {
  font-size: 18px;
  width: 1200px;
  margin: 0 auto;
  border-radius: 50px;
  background-color: #fff;
}
.carHeader {
  width: 100%;
  height: 80px;
  line-height: 80px;
  text-align: left;
  border-bottom: 1px solid #000;
  input {
    margin-left: 5%;
  }
  .all {
    margin-left: 1%;
  }
  .name {
    margin-left: 16%;
  }
  .pri {
    margin-left: 16%;
  }
  .price {
    margin-left: 10%;
  }
  .number {
    margin-left: 10%;
  }
  .handle {
    margin-left: 11%;
  }
}
.carMain {
  background-color: #f5f5f5;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 50px;
  width: 100%;
  text-align: left;
  height: 100px;
  line-height: 100px;
  img {
    width: 8%;
    height: 8%;
    vertical-align: middle;
    margin-left: 5%;
  }
  .dele {
    width: 80px;
    height: 50px;
    margin-left: 8%;
  }
  .mainItem {
    width: 100%;
    .done {
      margin-left: 5%;
    }
    .pri {
      display: inline-block;
      width: 100px;
      margin-left: 5%;
      // background-color: red;
      text-align: center;
    }
    .name {
      width: 20%;
      // background-color: red;
      margin-left: 1%;
      display: inline-block;
      text-align: center;
    }
    .num {
      display: inline-block;
      margin-left: 5%;
      button {
        width: 30px;
        height: 30px;
      }
      span {
        display: inline-block;
        text-align: center;
        width: 30px;
        height: 30px;
      }
    }
    .price {
      display: inline-block;
      width: 100px;
      margin-left: 5%;
      // background-color: red;
      text-align: center;
    }
  }
}
.carFooter {
  width: 100%;
  height: 80px;
  border-top: 1px solid #000;

  line-height: 80px;
  text-align: left;
  .delect {
    display: inline-block;
    width: 80px;
    // background-color: red;
    text-align: center;
    margin-left: 5%;
  }
  input {
    margin-left: 5%;
  }
  .all {
    margin-left: 1%;
  }
  .dele {
    width: 80px;
    height: 50px;
    margin-left: 4%;
  }
  .add {
    margin-left: 45%;
    width: 200px;
    display: inline-block;
    // background-color: red;
    text-align: center;
  }
}
</style>